<template>
  <div class="row justify-content-center pt-4">
    <div class="col-10 col-lg-3 text-center">
      <div class="text-center">
        <router-link :to="`/`">
          <img
            src="../../public/logo.webp"
            class="rounded"
            alt="Logo Beli Ajalah"
            style="height: 100px;"
          />
        </router-link>
      </div>
      <h3>Beli Ajalah</h3>
    </div>
  </div>

  <div class="row justify-content-center pt-4">
    <div class="col-10 col-lg-3 text-center">
      <div class="form-group">
        <input
          v-model="formLogin.username"
          class="form-control"
          placeholder="Username"
          required
        />
      </div>

      <div class="form-group mt-3">
        <input
          v-model="formLogin.password"
          class="form-control"
          type="password"
          placeholder="Password"
          required
        />
      </div>

      <div class="text-center">
        <button
          class="btn btn-outline-primary mt-4 col-6"
          @click="login(formLogin)"
        >
          Login
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { useLoadUsers } from '@/firebase'
import { reactive } from 'vue'

export default {
  data() {
    return {
      users: useLoadUsers(),
      router: useRouter(),
      formLogin: reactive({ username: '', password: '' })
    }
  },
  methods: {
    login(formLogin) {
      if (
        this.users[0].username == formLogin.username &&
        this.users[0].password == formLogin.password
      ) {
        this.router.push('/admin')
      } else {
        this.router.push('/')
        alert('Username atau Password salah !')
      }
    }
  }
}
</script>
