<template>
  <div class="card card-body mt-4">
    <h3>Edit Link</h3>
    <form @submit.prevent="update">
      <div class="form-group">
        <label>Name</label>
        <input v-model="form.name" class="form-control" required />
      </div>

      <div class="form-group mt-3">
        <label>Link</label>
        <input v-model="form.link" class="form-control" required />
      </div>

      <button type="submit" class="btn btn-primary  mt-3">
        Update
      </button>
    </form>
  </div>
</template>

<script>
import { reactive, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { getLink, updateLink } from '@/firebase'

export default {
  setup() {
    const router = useRouter()
    const route = useRoute()
    const linkId = computed(() => route.params.id)

    const form = reactive({ name: '', email: '' })
    onMounted(async () => {
      const user = await getLink(linkId.value)
      console.log(user, linkId.value)
      form.name = user.name
      form.link = user.link
    })

    const update = async () => {
      await updateLink(linkId.value, { ...form })
      router.push('/')
      form.name = ''
      form.link = ''
    }

    return { form, update }
  }
}
</script>
