<template>
  <div class="row justify-content-center pt-4">
    <div class="col-10 col-lg-3 text-center">
      <div class="text-center">
        <router-link :to="`/login`">
          <img
            src="../../public/logo.webp"
            class="rounded"
            alt="Logo Beli Ajalah"
            style="height: 100px;"
          />
        </router-link>
      </div>
      <h3>Beli Ajalah</h3>
    </div>
  </div>

  <div class="row justify-content-center pt-1">
    <div class="col-4 col-lg-3 text-center">
      <h6>Pencarian</h6>
      <input
        class="form-control text-center"
        type="text"
        v-model="search"
        @keyup="filtered()"
        @keyup.delete="filtered()"
        placeholder="Ketik No"
        aria-label="Search"
      />
    </div>
  </div>

  <div class="row justify-content-center pt-3">
    <div
      v-for="{ id, name, link } in filtered()"
      :key="id"
      class="col-10 col-lg-3 btn-outline-primary text-center border rounded my-1"
    >
      <a :href="link" class="link-dark" style="text-decoration:none;"
        ><h5 class="pt-1">{{ name }}</h5></a
      >
    </div>
  </div>
</template>

<script>
import { useLoadLinks } from '@/firebase'

export default {
  data() {
    return {
      links: useLoadLinks(),
      search: ''
    }
  },
  methods: {
    filtered() {
      return this.links.filter(link => {
        return link.name.match(this.search)
      })
    }
  }
}
</script>
