<template>
  <div class="card card-body mt-4">
    <form @submit.prevent="onSubmit">
      <div class="form-group">
        <input
          v-model="form.name"
          class="form-control"
          placeholder="Title"
          required
        />
      </div>

      <div class="form-group mt-3">
        <input
          v-model="form.link"
          class="form-control"
          placeholder="Link"
          required
        />
      </div>

      <div class="text-center">
        <button type="submit" class="btn btn-outline-primary btn-sm mt-4 col-6">
          Add Link
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { createLink } from '@/firebase'
import { reactive } from 'vue'

export default {
  setup() {
    const form = reactive({ name: '', link: '' })

    const onSubmit = async () => {
      await createLink({ ...form })
      form.name = ''
      form.link = ''
    }

    return { form, onSubmit }
  }
}
</script>
