<template>
  <div class="row justify-content-center pt-2">
    <div class="col-4 col-lg-3 text-center">
      <input
        class="form-control text-center"
        type="text"
        v-model="search"
        @keyup="filtered()"
        @keyup.delete="filtered()"
        placeholder="Ketik No"
        aria-label="Search"
      />
    </div>
  </div>

  <div class="card mt-2 mb-4">
    <table class="table m-0">
      <thead>
        <tr class="ps-3">
          <th class="col-8 col-lg-11 ps-4" scope="col">Title</th>
          <th class="col-3 col-lg-1" scope="col">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="{ id, name } in filtered()" :key="id">
          <td class="ps-4">{{ name }}</td>
          <td>
            <router-link :to="`/edit/link/${id}`">
              <button class="btn btn-primary btn-sm me-2">
                <i class="far fa-edit"></i>
              </button>
            </router-link>
            <button class="btn btn-danger btn-sm" @click="deleteLink(id)">
              <i class="fas fa-trash-alt"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { useLoadLinks, deleteLink } from '@/firebase'

export default {
  setup() {
    return { deleteLink }
  },
  data() {
    return {
      links: useLoadLinks(),
      search: ''
    }
  },
  methods: {
    filtered() {
      return this.links.filter(link => {
        return link.name.match(this.search)
      })
    }
  }
}
</script>
