import firebase from 'firebase'
import { ref, onUnmounted } from 'vue'

const config = {
  apiKey: 'AIzaSyBqIDZ-AvXZ8Rxp5QslvCqzFAK8Ivg-TG8',
  authDomain: 'beli-ajalah.firebaseapp.com',
  projectId: 'beli-ajalah',
  storageBucket: 'beli-ajalah.appspot.com',
  messagingSenderId: '642966406271',
  appId: '1:642966406271:web:62a2e7edf63a6d2022eb4a',
  measurementId: 'G-WQ4JF3JHRN'
}

const firebaseApp = firebase.initializeApp(config)
const db = firebaseApp.firestore()
const usersCollection = db.collection('users')
const linksCollection = db.collection('links')

export const createUser = user => {
  return usersCollection.add(user)
}

export const createLink = link => {
  return linksCollection.add(link)
}

export const getUser = async id => {
  const user = await usersCollection.doc(id).get()
  return user.exists ? user.data() : null
}

export const getLink = async id => {
  const link = await linksCollection.doc(id).get()
  return link.exists ? link.data() : null
}

export const updateUser = (id, user) => {
  return usersCollection.doc(id).update(user)
}

export const updateLink = (id, link) => {
  return linksCollection.doc(id).update(link)
}

export const deleteUser = id => {
  return usersCollection.doc(id).delete()
}

export const deleteLink = id => {
  return linksCollection.doc(id).delete()
}

export const useLoadUsers = () => {
  const users = ref([])
  const close = usersCollection.onSnapshot(snapshot => {
    users.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
  })
  onUnmounted(close)
  return users
}

export const useLoadLinks = () => {
  const links = ref([])
  const linksDesc = db.collection('links').orderBy('name', 'desc')
  const close = linksDesc.onSnapshot(snapshot => {
    links.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
  })
  onUnmounted(close)
  return links
}
